___CSS_LOADER_EXPORT___.push([module.id, `:root {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  width: 100vw;
  min-height: 100vh;
  overflow-y: auto;
  font-family: Helvetica, sans-serif;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}

/* Header */

header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10vh;
  gap: clamp(10px, 20px, 30%);
  padding: 0 5px;
  color: white;
}

.logo {
  width: 40px;
  height: auto;
}

.logo-section,
.search-bar {
  display: flex;
  align-items: center;
  gap: 10px;
}

.app-title {
  font-size: 20px;
  text-align: center;
}

.search-bar {
  position: relative;
  width: clamp(100px, 80%, 500px);
}

#search-input {
  width: 100%;
  padding: 10px;
  background-color: white;
  border: 1px solid black;
  border-radius: 5px;
  font-size: 15px;
}

.search-btn {
  position: absolute;
  right: 5px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

/* Main Content */

.main-content {
  justify-self: center;
  background-color: white;
  width: 80%;
  height: 70%;
  display: grid;
  grid-template-columns: repeat(2, minmax(200px, 1fr));
  grid-template-rows: 4fr 3fr;
  gap: 10px;
  background-color: transparent;
}

.weather-today {
  grid-area: 1/1/3/2;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid black;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.location-info {
  font-weight: bold;
}

.location {
  font-size: 40px;
}

.current-date {
  font-size: 20px;
  color: gray;
}

.temperature {
  font-size: 70px;
}

.weather-details {
  background-color: rgba(0, 0, 54, 0.5);
  font-size: 20px;
  padding: 20px;
}

.descriptions {
  margin-bottom: 20px;
}

.feels-like,
.humidity,
.rain-prob,
.wind-speed,
.uv-index {
  border-bottom: 1px solid lightgray;
  padding: 10px;
  margin: 5px;
}

.five-day-forecast {
  background-color: rgba(0, 0, 54, 0.5);
  font-size: 15px;
  padding: 20px;
}

.forecast-container {
  display: flex;
  margin-top: 10px;
  align-items: top;
  gap: 10px;
  height: 80%;
}

.icon {
  width: 60%;
  height: auto;
  margin: 5px 0;
}

.forecast-container > div {
  min-width: 0;
  font-size: clamp(10px, 50%, 15px);
  white-space: normal;
  background-color: rgba(0, 0, 54, 0.5);
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  padding: 5px;
}

.title {
  font-size: 20px;
}

input[type='range'] {
  width: 80%;
}

@media (max-width: 900px) {
  .main-content {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }

  .weather-today {
    grid-area: auto;
    padding: 20px;
  }
}
